@import url('https://fonts.googleapis.com/css2?family=Sedgwick+Ave+Display&display=swap');

/********************* tablet horizantal  *************************************/
@media only screen and (min-width:  768px) and (max-width: 1024px){
* {
    margin:0;
    padding:0;
    box-sizing: border-box;
}

.welcome {
    display: grid;
    height:100vh;
    width: 100%;
    background-color: #8FA677;
    grid-template-columns: 55% 5% 35% 5% ;
    grid-template-rows: 100% ;
}

.welcomeImage{
    height:100vh;
    width: 100%;
    grid-column-start: 1;
    grid-column-end: 2;
    position: relative;
    background-image: url('../assets/fondo-principal.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    background-color: #8FA677;
}

.title {
    position: absolute;
    max-height: 14vh;
    width: 100%;
    background: #8FA677A1;
    border: #8FA677 solid ;
    top: 70%;
    text-align: center;
    font-family: 'Sedgwick Ave Display', cursive;
}

.title h1 {
    font-size: 6vw;
}

.welcomeButtons{
    height:100vh;
    width: 100%;
    grid-column-start: 3;
    grid-column-end: 4;
}

.welcomeButtons img {
    width: 100%;
    height: 100%;
    border: 5px solid #B4BD7E;
    mix-blend-mode: multiply;
}  


.containerButtons{
    display: grid;
    height:100vh;
    width: 100%;
    grid-template-columns: 100% ;
    grid-template-rows: 10% 30% 5% 30% 5% 10% 10%  ;
    color: white;
    text-decoration: none;
}

.containerButtons a{
    color: rgb(235, 255, 195);
}

.containerLogin,
.containerRegister {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
}

.containerLogin{
    grid-row-start: 2;
    grid-row-end:3 ;
}

.containerRegister{
    grid-row-start: 4;
    grid-row-end:5 ;
}

 .textLogin,
 .textRegister {
    position: absolute;
    top: 1vh;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    padding: 0em 0.5em;
    font-size: 3.5vh;
 }

 .titleLogin,
 .titleRegister {
    position: absolute;
    top: 20vh;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    font-size: 4vh;
 }



.about {
    width: 100%;
    height: 100%; 
    grid-row-start: 6;
    grid-row-end:7;
}

.about button {
    width: 100%;
    height: 100%; 
    cursor: pointer;
    background: #B4BD7E;
    border: none;
    font-size: 2em;
    padding: 0.5em 0.5em;
}
}


